import React, { useState, useEffect } from 'react';
import propTypes from 'prop-types';
import styles from './css/appointmentList.module.css';
import { connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import { selectItemUpdate as selectItemUpdateAction,
         deleteAppointment as deleteAppointmentAction, 
} from '../../redux/actions/appointmentsActions';

const AppointmentList = (props) => {
        const {
            _id,
            building,
            boiler,
            technician,
            start_timestamp,
            end_timestamp,
            monthly_hours,
            maintainceType
        } = props.appointment;

        return (  
            <tr className={styles.tRow}>
                <td className={styles.data}>{_id}</td>
                <td className={styles.data}>{building && building.fullName}</td>
                <td className={styles.data}>{boiler && boiler.description}</td>
                <td className={styles.data}>{technician.firstName} - {technician.lastName}</td>
                <td className={styles.data}>{start_timestamp}</td>
                <td className={styles.data}>{end_timestamp}</td>
                <td className={styles.data}>{monthly_hours}</td>
                <td className={styles.data}>{maintainceType}</td>
                <td className={styles.btn}>
                    <button className={styles.xBtn} onClick={() => props.selectItemUpdate(props.appointment)}>EDIT</button>
                </td>
                <td className={styles.btn}>
                    <button className={styles.xBtn} onClick={() => props.deleteAppointment(_id)}>DELETE</button>
                </td>
            </tr>
        );  
};

AppointmentList.propTypes = {
    appointment: propTypes.array.isRequired
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItemUpdate: selectItemUpdateAction,
        deleteAppointment: deleteAppointmentAction
    },dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(AppointmentList)
