import React, { useState, useEffect} from 'react'
import propTypes from 'prop-types';
import styles from './css/boilerList.module.css'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { 
    selectBoiler as selectBoilerAction,
    delBoilers as delBoilersAction,
} from '../../redux/actions/boilersActions';

function BoilersList (props) {
    
    return (
        <tr className={styles.tRow} >
            <td className={styles.data}>{props.boiler._id}</td>
            <td className={styles.data}>{props.boiler.description}</td>
            <td className={styles.data}>{props.boiler.boilerType.description}</td>
            <td className={styles.data}>{(props.boiler.building && props.boiler.building.address) || ''}</td>
            <td className={styles.data}>{props.boiler.maintenancePeriod}</td>
            <td className={styles.data}>{props.boiler.hourMaintenanceCost}</td>
            <td className={styles.data}>{props.boiler.hourEventualCost}</td>
            <td className={styles.btn}>
                <button className={styles.xBtn} onClick={() =>props.selectItem(props.boiler)}> 
                    EDIT 
                </button>
            </td>
            <td className={styles.btn}>

                <button className={styles.xBtn} onClick={() =>props.deleteBoiler(props.boiler._id)} > 
                    DELETE
                </button>
            </td>
        </tr>
    )
}

BoilersList.propTypes = {
    boiler: propTypes.object.isRequired
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItem: selectBoilerAction, 
        deleteBoiler: delBoilersAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(BoilersList);
