import {
    deleteBuilding as deleteBuildingAction,
    selectBuilding as selectBuildingAction,
} from '../../redux/actions/buildings';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import propTypes from 'prop-types';
import styles from './BuildingsItem.module.css';

const BuildingItem = ({
    building,
    deleteBuilding,
    selectBuilding
}) => (
    <tr className={styles.tRow}>
        <td className={styles.data}>{building.fullName}</td>
        <td className={styles.data}>{building.company && building.company.companyName}</td>
        <td className={styles.data}>{building.address}</td>
        <td className={styles.data}>{building.phone}</td>
        <td className={styles.btn}>
            <button className={styles.xBtn} onClick={() => selectBuilding(building)}>EDIT</button>
        </td>
        <td className={styles.btn}>
            <button className={styles.xBtn} onClick={() => deleteBuilding(building._id)}>DELETE</button>
        </td>
    </tr>
);

BuildingItem.propTypes = {
    building: propTypes.object.isRequired
}


const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        deleteBuilding: deleteBuildingAction,
        selectBuilding: selectBuildingAction,
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(BuildingItem);