import React, { useState, useEffect} from 'react'
import propTypes from 'prop-types';
import styles from './css/typesList.module.css';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { 
    selectBoilerTypes as selectBoilerTypesAction,
    delBoilerTypes as delBoilerTypesAction,
} from '../../redux/actions/boilerTypesActions';

function TypesList (props) {

    return (
        <tr className={styles.tRow}>
            <td className={styles.data}>{props.boilerType._id}</td>
            <td className={styles.data}>{props.boilerType.description}</td>
            <td className={styles.btn}>
                <button className={styles.xBtn} onClick={() =>props.selectItem(props.boilerType)}> 
                    EDIT 
                </button>
            </td>
            <td className={styles.btn}>
                <button  className={styles.xBtn} onClick={() =>props.deleteType(props.boilerType._id)} > 
                    DELETE
                </button>
            </td>
        </tr>
    )
}

TypesList.propTypes = {
    boilerType: propTypes.object.isRequired
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItem: selectBoilerTypesAction, 
        deleteType: delBoilerTypesAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(TypesList);
