import React from 'react';
import propTypes from 'prop-types';
import BuildingItem from './BuildingItem';
import styles from './BuildingsTable.module.css';

const BuildingsTable = (props) => (
    <div className={styles.container}>
        <table className={styles.table}>
            <thead className={styles.thead}>
                <tr className={styles.tr}>
                    <th className={styles.th}>Building Name</th>
                    <th className={styles.th}>Company Name</th>
                    <th className={styles.th}>Address</th>
                    <th className={styles.th}>Phone</th>
                    <th className={styles.btn}>EDIT</th>
                    <th className={styles.btn}>DELETE</th>
                </tr>
            </thead>
            <tbody className={styles.tbody}>
                {props.buildings.map(building => (
                    <BuildingItem
                        key={building._id}
                        building={building}
                    />
                ))}
            </tbody>
        </table>
    </div>
);

BuildingsTable.propTypes = {
    buildings: propTypes.array.isRequired
}

export default BuildingsTable;
