import React from 'react'
import { bindActionCreators } from 'redux';
import {connect} from 'react-redux';

import { showModal, closeModal } from '../../redux/actions/modalActions';
import { delCompany, companySelected } from '../../redux/actions/companiesActions';


const DeleteCompanyModal = ({ _id, showModal, closeModal, delCompany }) => {
    
    const callDelCompany = (_id) => {
        delCompany(_id);
        closeModal();
    }

    return (
        <div>
            <h2>
            Delete Companie {_id}
            </h2>
            <p>Are you sure you want to delete this company?</p>
            <div>
                <button onClick={closeModal} >CANCEL</button>
                <button onClick={() => callDelCompany(_id)} >DELETE</button>
            </div>
        </div>

    )
}


const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        delCompany: delCompany,
        showModal: showModal,
        closeModal: closeModal,
    }, dispatch)
}

export default connect(null, mapDispatchToProps)(DeleteCompanyModal);