import React, { useState, useEffect } from 'react';
import styles from './techniciansList.module.css';
import propTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { selectItemUpdate as selectItemUpdateAction,
         deleteTechnician as deleteTechnicianAction, 
} from '../../../redux/actions/techniciansActions.js';

const TechniciansList = (props) => {

        const {
            _id,
            firstName,
            lastName,
            phone,
            birthdate,
            capabilities,
            hour_rate,
            monthly_capacity
        } = props.technician;

        return  (
            <tr className={styles.tRow}>
                <td className={styles.data}>{_id}</td>
                <td className={styles.data}>{firstName}</td>
                <td className={styles.data}>{lastName}</td>
                <td className={styles.data}>{phone}</td>
                <td className={styles.data}>{birthdate}</td>
                <td className={styles.data}>
                    {capabilities && capabilities.map(capability => {
                    return (`${capability.description} `)
                    })}
                </td>
                <td className={styles.data}>{hour_rate}</td>
                <td className={styles.data}>{monthly_capacity}</td>
                <td className={styles.btn}>
                    <button className={styles.xBtn} onClick={() => props.selectItemUpdate(props.technician)}>EDIT</button>
                </td>
                <td className={styles.btn}>
                    <button className={styles.xBtn} onClick={() => props.deleteTechnician(_id)}>DELETE</button>
                </td>
            </tr>
        )
}

TechniciansList.propTypes = {
    technician: propTypes.object.isRequired
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItemUpdate: selectItemUpdateAction,
        deleteTechnician: deleteTechnicianAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(TechniciansList);
