import React, { useEffect } from 'react';
import { bindActionCreators } from 'redux';
import {connect} from 'react-redux';

import { Form, Field} from 'react-final-form';

import { addCompany, updateCompany, toEditAction, cleanToEdit, } from '../../../redux/actions/companiesActions'
import { closeModal } from '../../../redux/actions/modalActions';

const CompaniesForm = ({ addCompany, toEditAction, updateCompany, toEdit, newCompany, closeModal, cleanToEdit, }) => {

    //VALIDATION REGEX's
    const fullNamePatt = /[a-zA-Z]+/;
    const emailPatt = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    const cuitPatt = /[0-9]{11}/;
    const fiscalAddressPatt = /[a-zA-Z]{5,}\s\d+/;
    
    useEffect(() => {
        toEditAction({
            _id: newCompany._id,
            companyName: newCompany.companyName,
            email: newCompany.email,
            fiscalAddress: newCompany.fiscalAddress,
            cuit: newCompany.cuit,
        });
    }, [newCompany]);

    useEffect(() => {
       return () => cleanToEdit();
       
    },[]);

 
    const onSubmit = (values) => {
        if(!values._id){
            addCompany({
                companyName: values.companyName, 
                cuit: values.cuit, 
                email: values.email, 
                fiscalAddress: values.fiscalAddress
            });
        }
        else {
            updateCompany(
                values
            )
        };
        closeModal();
    };


    const calCloseModal = () => {
        closeModal();
    }
    
    return (
        <div>
            <Form onSubmit={onSubmit} initialValues={toEdit}>
                {({ handleSubmit, submitting, pristine, form }) => (
                    <form onSubmit={handleSubmit}>

                        <Field
                            name="companyName"
                            afterSubmit={form.reset}
                            validate={value => {
                                if (!value) {
                                    return 'Required'
                                }
                                else if (!fullNamePatt.test(value)) {
                                    return 'Must be a word'
                                }
                            }}                                    
                        >
                            {({ input, meta }) => (
                                <div>
                                    <label>Company Name: </label>
                                    <input  {...input} placeholder="Company Name" />  
                                    {meta.error && meta.touched && <span>{meta.error}</span>}
                                </div>
                            )}
                        </Field>

                        <Field
                            name="email"
                            afterSubmit={form.reset}
                            validate={value => {
                                if (!value) {
                                    return 'Required'
                                }
                                else if (!emailPatt.test(value)) {
                                    return 'Must be a valid email'
                                }
                            }}                                    
                        >
                            {({ input, meta, placeholder }) => (
                                <div>
                                    <label>Email: </label>
                                    <input  {...input} placeholder="example@mail.com" />  
                                    {meta.error && meta.touched && <span>{meta.error}</span>}
                                </div>
                        )}
                        </Field>

                        <Field
                            name="fiscalAddress"
                            afterSubmit={form.reset}
                            validate={value => {
                                if (!value) {
                                    return 'Required'
                                }
                                else if (!fiscalAddressPatt.test(value)) {
                                    return 'Must be valid address'
                                }
                            }}                                    
                        >
                            {({ input, meta, placeholder }) => (
                                <div>
                                    <label>Fiscal Address: </label>
                                    <input  {...input} placeholder="example 123 4º a" />  
                                    {meta.error && meta.touched && <span>{meta.error}</span>}
                                </div>
                        )}
                        </Field>

                        <Field
                            name="cuit"
                            afterSubmit={form.reset}
                            validate={value => {
                                if (!value) {
                                    return 'Required'
                                }
                                else if (!cuitPatt.test(value)) {
                                    return 'Must contain 11 characters'
                                }
                            }}                                    
                        >
                            {({ input, meta, placeholder }) => (
                                <div>
                                    <label>Cuit: </label>
                                    <input  {...input} placeholder="Cuit" />  
                                    {meta.error && meta.touched && <span>{meta.error}</span>}
                                </div>
                        )}
                        </Field>

                        <div>
                            <button type="submit" disabled={submitting || pristine} >SUBMIT</button>
                        </div>

                        <div>
                            <button type="button" onClick={form.reset} >RESET</button>
                        </div>
                        <div>
                            <button type="button" onClick={() => calCloseModal()} >CANCEL</button>
                        </div>
                    </form>
                )}
            </Form>
        </div>
    )
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        addCompany: addCompany,
        updateCompany: updateCompany,
        toEditAction: toEditAction,
        closeModal: closeModal,
        cleanToEdit: cleanToEdit,
    }, dispatch)
}

const mapStateToProps = state => {
    return {
        companies: state.companies.list,
        newCompany: state.companies.newCompany,
        isLoading: state.companies.isLoading,
        toEdit: state.companies.toEdit,
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CompaniesForm);