import React from 'react';
import CompanyData from '../CompanyItem/CompanyData';
import styles from '../css/companyTable.module.css';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { showModal } from '../../../redux/actions/modalActions';
import modalTypes from '../../../redux/types/modalTypes'


const CompanyTable = ({companies, showModal}) => {
    return(
    <div className={styles.container}>
        <table className={styles.table}>
            <thead className={styles.thead}>
                <tr className={styles.tr}>
                    <th className={styles.th}>Company Name</th>
                    <th className={styles.th}>Email</th>
                    <th className={styles.th}>Fiscal Address</th>
                    <th className={styles.th}>Cuit</th>
                    <th className={styles.th}>Company ID</th>
                    <th className={styles.btn}>UPDATE</th>
                    <th className={styles.btn}>DELETE</th>
                </tr>
            </thead>

            <tbody className={styles.tbody}>
                {console.log(companies)}
                {companies.map((company) => 
                    <CompanyData 
                        key={company._id} 
                        company={company} 
                    />
                )}
            </tbody>
            <button className={styles.addBtn} onClick={() => showModal(modalTypes.COMPANIES_FORM) } >+</button>    
        </table>
    </div>
    )
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        showModal: showModal,
    }, dispatch)
}

const mapStateToProps = state => {
    return {
        companies: state.companies.list,
    }

}

export default connect(mapStateToProps, mapDispatchToProps)(CompanyTable);