import React, { useState, useEffect} from 'react'
import propTypes from 'prop-types';
import TypesList from './BoilerTypesList';
import styles from './css/typesTable.module.css';

function TypesTable (props) {
   
    return (
        <div>
            <div className={styles.container}> 
                <table className={styles.table}>
                    <thead className={styles.thead}>
                        <tr className={styles.tr}>
                            <th className={styles.th}>ID</th>
                            <th className={styles.th}>Description</th>
                            <th className={styles.btn}>UPDATE</th>
                            <th className={styles.btn}>DELETE</th>
                        </tr>
                    </thead>
                    <tbody className={styles.tbody}>
                        {props.boilerTypes.map((boilerType) => (
                            <TypesList key={boilerType.id} boilerType={boilerType} />
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

TypesTable.propTypes = {
    boilerTypes: propTypes.array.isRequired
}

export default TypesTable;