import React, { useState, useEffect} from 'react'
import propTypes from 'prop-types';
import BoilersList from './BoilersList';
import styles from './css/boilersTable.module.css';

function BoilersTable (props) {
   
    return (
        <div>
            <div className={styles.container}> 
                <table className={styles.table}>
                    <thead className={styles.thead}>
                        <tr className={styles.tr}>
                            <th className={styles.th}>ID</th>
                            <th className={styles.th}>Description</th>
                            <th className={styles.th}>Boiler Type</th>
                            <th className={styles.th}>Building</th>
                            <th className={styles.th}>Maintenance Period</th>
                            <th className={styles.th}>Hour Maintenance Cost</th>
                            <th className={styles.th}>Hour Eventual Cost</th>
                            <th className={styles.btn}>UPDATE</th>
                            <th className={styles.btn}>DELETE</th>
                        </tr>
                    </thead>
                    <tbody className={styles.tbody}>
                        {props.boilers.map((boiler) => (
                            <BoilersList key={boiler.id} boiler={boiler} />
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

BoilersTable.propTypes = {
    boilers: propTypes.array.isRequired
}

export default BoilersTable;
