import React, { useState, useEffect } from 'react';
import AppointmentList from './AppointmentList';
import propTypes from 'prop-types';
import styles from './css/appointmentsTable.module.css';

const AppointmentsTable = (props) => {
    return (
        <div className={styles.container}>
            <table className={styles.table}>
                <thead className={styles.thead}>
                    <tr className={styles.tr}>
                        <th className={styles.th}>ID</th>
                        <th className={styles.th}>Building</th>
                        <th className={styles.th}>Boiler</th>
                        <th className={styles.th}>Technician</th>
                        <th className={styles.th}>Start time</th>
                        <th className={styles.th}>End time</th>
                        <th className={styles.th}>Montlhy hours</th>
                        <th className={styles.th}>Maintenance type</th>
                        <th className={styles.btn}>EDIT</th>
                        <th className={styles.btn}>UPDATE</th>
                    </tr>
                </thead>
                <tbody className={styles.tbody}>
                    {props.appointments && props.appointments.map((appointment) =>
                    <AppointmentList key={appointment.id} appointment={appointment}
                    />
                    )}
                </tbody>
            </table>
        </div>
    );
};

AppointmentsTable.propTypes = {
    appointments: propTypes.array.isRequired
};

export default AppointmentsTable